.editprofile_card{
    display: flex;
    justify-content: center;
}
.edit_card{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.edit_card img{
    height: 150px;
    width:150px;
    margin:5px auto 30px auto;
    border-radius: 75px;
}

.edit_card div{
    display:flex;
    flex-direction: row;
}
.edit_card h6{
    margin:auto 20px;
    font-weight: 500;
}
.pic_card{
    display: flex;
    flex-direction: row-reverse;
}
.pic_button{
    margin-bottom: 10px;
    width:70%;
    margin-left:35px;
  
}
.save_button{
    width:60%;
    margin-left: 40px;
    margin-top:20px
}