.home_card{
    max-width: 500px;
    height: max-content;
    margin:26px auto;
    padding: 5px 0;
}
.home_card h5{
    padding-left: 10px;
    margin-top:6px;
}
.username_link{
    color:black;
    font-weight: 600;
}
.home_card h5 i{
    float:right;
}
i{
    cursor: pointer;
}
.comment_div{
    display: flex;
    flex-direction: row;
}
.comment_div button{
    height: 30px;
    margin-top: auto;
    margin-bottom: auto;
    border:none;
    color:blue;
    cursor:pointer
}